<template>
  <div class="home" ref="home" @mousemove="load">
    <!-- <div class="div">ádjaslkdjsa</div> -->
    <div class="cursor" ref="cursor"></div>
    <header class="header">
      <div class="header-top">
        <div class="header-logo">Nothing</div>
        <ul class="header-menu">
          <li>Home</li>
          <li>Review</li>
          <li>Contact</li>
          <li>Blog</li>
          <li>About</li>
        </ul>
        <i class="fa fa-shopping-bag header-cart"></i>
      </div>
      <div class="header-content" ref="zoom">
        <div class="header-info">
          <h1 class="header-heading">Frontend Developer</h1>
          <h2 class="header-caption">Code by Nhu Phu</h2>
          <h3 class="header-title">Select your colors:</h3>
          <ul class="color">
            <li class="color-item"></li>
            <li class="color-item"></li>
            <li class="color-item"></li>
            <li class="color-item"></li>
            <li class="color-item-more">
              <i class="fa fa-angle-right"></i>
            </li>
          </ul>
          <div class="header-meta">
            <h3 class="header-price">$3000.00</h3>
            <button class="header-buynow">Start</button>
          </div>
        </div>
        <img
          src="https://www.officialgates.com/assets/images/banner-images/og_vue.png"
          alt=""
          class="header-image"
        />
      </div>
    </header>
  </div>
</template>

<script>
import { TimelineLite, TweenMax } from "gsap";

export default {
  name: "Home",
  components: {},
  methods: {
    init() {
      TweenMax.to(".header", 0, { visibility: "visible" });
      let tl = new TimelineLite();

      tl.from(
        ".header-logo",
        1,
        {
          y: -20,
          opacity: 0,
        },
        "timer"
      )
        .staggerFrom(
          ".header-menu li",
          1,
          {
            y: -20,
            opacity: 0,
          },
          0.25,
          "timer"
        )
        .from(
          ".header-cart",
          1,
          {
            y: -20,
            opacity: 0,
          },
          "-=1"
        )
        .from(
          ".header-heading",
          1,
          {
            x: -20,
            opacity: 0,
          },
          "-=0.75"
        )
        .from(
          ".header-caption",
          1,
          {
            x: -20,
            opacity: 0,
          },
          "-=0.75"
        )
        .from(
          ".header-title",
          1,
          {
            x: -20,
            opacity: 0,
          },
          "-=0.75"
        )
        .staggerFrom(
          ".color-item",
          1,
          {
            x: -10,
            opacity: 0,
          },
          0.125,
          "-=0.75"
        )
        .from(
          ".color-item-more",
          1,
          {
            x: -10,
            opacity: 0,
          },
          "-=0.75"
        )
        .from(
          ".header-price",
          1,
          {
            x: -20,
            opacity: 0,
          },
          "-=0.9"
        )
        .from(
          ".header-buynow",
          1,
          {
            x: -20,
            opacity: 0,
          },
          "-=0.75"
        )
        .from(
          ".header-image",
          1,
          {
            x: 50,
            opacity: 0,
          },
          "-=0.5"
        );
    },
    load(e) {
      let cursor = this.$refs.cursor;
      TweenMax.to(cursor, {
        x: e.clientX,
        y: e.clientY,
        duration: 0.2,
      });
      let zoom = this.$refs.zoom;
      zoom.addEventListener("mouseenter", () => {
        cursor.classList.add("zoom");
      });
      zoom.addEventListener("mouseleave", () => {
        cursor.classList.remove("zoom");
      });
    },
  },

  mounted() {
    // const gsap = new
    this.init();
    this.load();
  },
};
</script>
